body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

@import url(https://fonts.googleapis.com/css?family=Roboto);

body{
  background: rgba(26, 188, 156, 0.7);
}

.container{
  width: 100%;
  height: 100%;
}

.card-wrapper {
    width: 400px;
    margin: auto;
    margin-top: 100px;
}

.card{
  height: 220px;
  padding: 30px;
  padding-left: 50px;
  background-color: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: 0.3px 0.3px 0.3px 1px rgba(0, 0, 0, 0.2);
          box-shadow: 0.3px 0.3px 0.3px 1px rgba(0, 0, 0, 0.2);
  font-family: 'Roboto', sans-serif;
  position: relative;
}

.next-day__wrap {
    display: -ms-flexbox;
    display: flex;
}

.next-day {
    width: 300px;
    height: 220px;
    background-color: #fff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-box-shadow: 0.3px 0.3px 0.3px 1px rgba(0, 0, 0, 0.2);
            box-shadow: 0.3px 0.3px 0.3px 1px rgba(0, 0, 0, 0.2);
    padding: 30px;
}

.city, .date{
  color: gray;
}

.city{
  font-size: 26px;
}

.date{
  font-size: 20px;
  line-height: 40px;
  margin-bottom: 10px;
}

.weather {
    display: -ms-flexbox;
    display: flex;
}

.sun{
  display: inline;
	width: 80px;
	height: 80px;
}

.sun img {
    width: 100%;
}

.temp{
  padding-top: 6px;
  font-size: 50px;
  margin-left: 20px;
}

.card-input {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.card-input form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

input {
    width: 200px;
    height: 30px;
    color: grey;
    padding-left: 10px;
}

button {
    height: 36px;
    width: 80px;
    margin-left: 10px;
    background: #26a69a;
    border: none;
    color: white;
    text-transform: uppercase;
    letter-spacing: 1px;
    cursor: pointer;
}

form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.err-text {
    margin-bottom: 10px;
    color: red;
    margin-right: 80px;
}

.error input {
    border: 1px solid red;
}

.resolve_ico {
    width: 40px;
    position: absolute;
    top: 15px;
    right: 15px;
    -webkit-animation: locate  1.3s infinite;
            animation: locate  1.3s infinite;
}

.resolve_ico_grey {
    width: 40px;
    position: absolute;
    top: 15px;
    right: 15px;
}

.not_city {
  position: absolute;
  top: 15px;
  right: 15px;
  cursor: pointer;
  color: grey;
}

@-webkit-keyframes locate {
from {opacity: 1;}
50% {opacity: 0.4;}
to {opacity: 1;}
}

@keyframes locate {
from {opacity: 1;}
50% {opacity: 0.4;}
to {opacity: 1;}
}



